@property --angle {
  syntax: '<angle>';
  inherits: true;
  initial-value: 0%;
}
body {
  background-color: black;
}


nav ul {
  display:flex;
  list-style: none;
  background-color: transparent;
  margin-top: 0%;
  justify-content: right;
  box-sizing: border-box;
  font-weight: bolder;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  
}
nav li{
  padding : 1%;
}
nav a{
  position: relative;
  text-decoration: none;

  display: inline-block;
  padding: 0.5em 1em;
  color: aliceblue;
  z-index: 1;
  background-color: black;
 border-radius: 10%;
 box-shadow: 10px 0px 20px rgb(241, 5, 5);
  
}
nav span {
  padding: 3.9% 6% 0 0 ;
  position: absolute;
  background-image: conic-gradient( rgb(5, 5, 5), rgb(248, 10, 10),rgb(5, 5, 5),rgb(253, 47, 0));
  border-radius: 5px;
  top:0%;
  
 
  left: 63%;
  transition: .5s;
  z-index: 0;

  
  
}


nav li:nth-child(1):hover~span {
  left : 63;
  
  background-image: conic-gradient( rgb(5, 5, 5), rgb(248, 10, 10),rgb(5, 5, 5),rgb(253, 47, 0));
}
nav li:nth-child(2):hover~span {
  left : 69%;
  padding-left: 2%;
  opacity: 1;
}
nav li:nth-child(3):hover~span {
  left :77%;
  padding-left: 2%;
  opacity: 1;
}
nav li:nth-child(4):hover~span {
  left:86%;
  opacity: 1;
}
nav li:nth-child(5):hover~span {
  left:93%;
  padding-left: 2%;
  opacity: 1;
}

